      <tbody>\r\n                        <tr v-for=\"(name, index) in details\" :key=\"index\">\r\n                            <th scope=\"row\">{{ name }}</th>\r\n                            <td class=\"td-left\">:</td>\r\n                            <td class=\"td-left\">{{ getItem(name) }}</td>\r\n                        </tr>\r\n                    </tbody>\r\n                </table>\r\n                <div v-else>\r\n                    <loading />\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport UserProfileSvg from \"../../assets/svg/UserProfile\";\r\nimport { mapActions, mapGetters } from \"vuex\";\r\nimport Loading from \"../../components/Core/Loading.vue\";\r\n\r\nexport default {\r\n    name: \"TalentsDetail\",\r\n    components: {\r\n        UserProfileSvg,\r\n        Loading\r\n    },\r\n    mounted() {\r\n        this.getTalent();\r\n    },\r\n    data() {\r\n        return {\r\n            id: this.$route.params.id,\r\n            loading: false,\r\n            details: [\r\n                \"Name\",\r\n                \"Source\",\r\n                \"Address\",\r\n                \"Applied Position\",\r\n                \"DOB\",\r\n                \"Email\",\r\n                \"Gender\",\r\n                \"Last Education\",\r\n                \"Mobile Phone\",\r\n                \"NIK\",\r\n                \"Total Working Experience\",\r\n                \"University\"\r\n            ]\r\n        };\r\n    },\r\n    computed: {\r\n        ...mapGetters({\r\n            TALENT: \"talent/TALENT\"\r\n        })\r\n    },\r\n    methods: {\r\n        ...mapActions({\r\n            GET_TALENT: \"talent/GET_TALENT\"\r\n        }),\r\n        async getTalent() {\r\n            this.loading = true;\r\n\r\n            try {\r\n                await this.GET_TALENT(this.id);\r\n                this.$toast.success(\"Data loaded\");\r\n            } catch (err) {\r\n                this.$toast.error(\"Error! \" + err.response.statusText);\r\n                if (err.response.status == 404) {\r\n                    this.$toast.info(\"Redirecting you to main page ...\");\r\n                    await this.$delay(3000);\r\n                    this.$router.push({ name: \"talents\" });\r\n                }\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n        getItem(name) {\r\n            let res = name.toLowerCase().replaceAll(\" \", \"_\");\r\n            return this.TALENT.data[`${res}`];\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\nth {\r\n    padding: 0.25rem;\r\n    width: 10rem;\r\n    font-weight: 700;\r\n}\r\n\r\ntd {\r\n    padding: 0.25rem;\r\n}\r\n\r\n.td-left {\r\n    text-align: left;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
